import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { listComicDetails } from "../actions/comicsActions";
// import { useParams } from "react-router-dom";
//MaterialUI
import CssBaseline from "@material-ui/core/CssBaseline";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import Typography from "@material-ui/core/Typography";
import { Link } from "react-router-dom";
import { ListGroup, Image } from "react-bootstrap";
import Loader from "../components/Loader";
import Message from "../components/Message";
const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
  singleMedia: {
    display: "flex",
  },
}));

export default function Post({ match }) {
  const slug = match.params.slug;
  // const { slug } = useParams();
  const dispatch = useDispatch();
  const classes = useStyles();
  const { comic, error, loading, chapters } = useSelector(
    (state) => state.comicDetails
  );
  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;

  useEffect(() => {
    dispatch(listComicDetails(slug));
  }, [dispatch, slug]);

  return (
    <Container component="main" maxWidth="md">
      <CssBaseline />
      <div className={classes.paper}> </div>{" "}
      {userInfo ? (
        <>
          {loading ? (
            <Loader />
          ) : error ? (
            <Message variant="danger">{error}</Message>
          ) : (
            <div className={classes.heroContent}>
              <Container maxWidth="sm">
                <Image
                  fluid
                  className={classes.singleMedia}
                  src={comic?.images}
                  alt={comic?.title}
                />
                <Typography
                  component="h1"
                  variant="h2"
                  align="center"
                  color="textPrimary"
                  gutterBottom
                >
                  {comic?.title}{" "}
                </Typography>{" "}
                <Typography
                  variant="h5"
                  align="center"
                  color="textSecondary"
                  paragraph
                >
                  {comic?.description}{" "}
                </Typography>{" "}
                <Typography
                  variant="h5"
                  align="center"
                  color="textSecondary"
                  paragraph
                >
                  {comic?.status}{" "}
                </Typography>{" "}
                <ListGroup variant="flush">
                  {chapters?.map((chapter) => (
                    <ListGroup.Item key={chapter.id}>
                      <Link to={`/chapter/${chapter.name}/`}>
                        {chapter.name}
                      </Link>
                    </ListGroup.Item>
                  ))}
                </ListGroup>
              </Container>{" "}
            </div>
          )}
        </>
      ) : (
        <>
          <Link to="/login">Login</Link>
        </>
      )}
    </Container>
  );
}
